import React, { Component } from 'react';
import { Form, Row, Col, Divider, Tooltip, Button, Message, Select } from 'antd';

import {
  SaveOutlined,
  CloseCircleOutlined
} from '@ant-design/icons';

import Api from '../../../helpers/api';

const FormItem = Form.Item;

const api = new Api();

export class PopupAssignUser extends Component {
  constructor() {
    super()
    this.state = {
      q: '',
      users: [],
      formAssignRole: {
        user_id: []
      },
      errors: {},
      loading: false
    }
  }

  componentDidMount() {
    // const { data } = this.props
    // console.log('didmount popup', this.props)
    this.fetchUserList()
  }

  fetchUserList() {
    console.log('q: ' + this.state.q)
    api.getAllUsers({ q: this.state.q })
      .then(response => {
        console.log(response.data);
        if (response.status === 200) { //OK
          const { data } = response.data
          this.setState({
            users: data,
          })
        }
      })
  }

  handleSearchUser = (event) => {
    console.log(event.target.value)

    this.setState({
      q: event.target.value
    }, () => {
      this.fetchUserList()
    }
    )
  }

  handleSelectedUser = (key, value) => {
    // console.log(value)
    let formAssignRoleNew = { ...this.state.formAssignRole }

    formAssignRoleNew['user_id'] = value.map(item => {
      return item.key
    })

    this.setState({
      formAssignRole: formAssignRoleNew
    }, () => {
      console.log(this.state.formAssignRole)
    })
  }

  handleSubmit = (e) => {

    this.setState({ loading: true });
    let apiPost
    const status = false

    //handling if form edit
    const { role } = this.props
    const { id_hash } = role
    const { id } = role
    const { formAssignRole } = this.state

    apiPost = api.updateUserList(id_hash ? id_hash : id, formAssignRole)

    apiPost.then(response => {
      // console.log(data);
      Message.success('save data success');
      this.setState({ loading: false, errors: {}, formAssignRole: { user_id: [] } })
      this.props.closeModal(status)
    }).catch(error => {
      this.setState({ loading: false })
      // console.log(error.response.data)
      // Message.error(error)
      // // handleApiError(error, this.props)
      // this.props.closeModal(status)
      if (error.response && error.response.data) {
        const { errors } = error.response.data
        this.setState({ errors: errors })
        console.log(errors.user_id)
      }
    })
  }


  render() {
    const { errors } = this.state;
    const { Option } = Select;

    return (
      <div>
        <Form className="mnd-form"
          onFinish={this.handleSubmit}>

          {/* <Row> */}
          {/* <Col md={24}> */}
          <div>
            <FormItem
              label="Email"
              validateStatus={errors && errors.user_id ? 'error' : undefined}
              help={errors ? errors.user_id : null}
              rules={[{ required: true, message: 'Please input email' }]}
            >
              <Select
                mode="multiple"
                placeholder="Select User List"
                allowClear={true}
                onKeyUp={this.handleSearchUser}
                onChange={this.handleSelectedUser}
                defaultValue={this.state.formAssignRole.user_id}
              >
                {
                  this.state.users.map(item => {
                    return (<Option key={item.id} value={item.email}>{item.email}</Option>)
                  })
                }
              </Select>
            </FormItem>
          </div>

          <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required field</p>

          {/* </Col> */}
          {/* </Row> */}
          <Divider />

          <FormItem>
            <Tooltip title="Clear">
              <Button style={{ marginLeft: 8 }}
                onClick={this.handleReset}>
                <CloseCircleOutlined />
              </Button>
            </Tooltip>

            <Tooltip title="Save">
              <Button loading={this.state.loading}
                type="primary" htmlType="submit" style={{ marginLeft: 10 }} >
                <SaveOutlined />
              </Button>
            </Tooltip>
          </FormItem>
        </Form>
      </div>
    )
  }
}

export default PopupAssignUser