import React from 'react';
import { Table, Input, Row, Col, Tooltip, message, Modal, Button, Icon } from 'antd';
//helpers
import Api from '../../../helpers/api';
import dateFormat from '../../../helpers/dateFormat'
import hasAuthObject from '../../../helpers/hasAuthObject'

import PopupAssignUser from './PopupAssignUser'

import {
  DeleteOutlined,
  PlusOutlined
} from '@ant-design/icons';
import handleApiError from '../../../helpers/handleApiError';

// import '@/assets/styles/global.css'
const api = new Api();

// setup auth object config for this page
const authobjectCfg = {
  view: 'role-view', //button display detail
  create: 'role-create', //button add
  update: 'role-update', //button edit, delete
}

class Page extends React.Component {
  constructor() {
    super()
    this.state = {
      data: [],

      q: '', //query for search
      selectedUserKeys: [],
      selectedRowKeys: [],
      pagination: {},
      loading: false,
      visible: false,
      visibleCL: false,
    }
    this.massDeleteData = this.massDeleteData.bind(this)
    this.confirmMassDeleteData = this.confirmMassDeleteData.bind(this)
  }

  componentDidMount() {
    // console.log('didmount table', this.props)
    const { role, data } = this.props
    this.setState({ role, selectedUserKeys: data })

    this.fetch();
  }

  handleTableChange = (pagination, filters, sorter) => {
    // console.log(filters);
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.fetch({
      per_page: pagination.pageSize,
      page: pagination.current,
      sort_field: sorter.field,
      sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
      ...filters,
    });
  }

  searchData = (value) => {
    this.setState({ q: value }, () => { this.fetch() })
  }

  fetch = (params = {}) => {
    const { role } = this.props

    // this.setState({role, selectedUserKeys: data })
    // console.log("data", this.props)

    // console.log('params:', params);
    const { q } = this.state
    this.setState({ loading: true });

    api.getUserListByRole(role.id, { ...params, q })
      .then(response => {
        // console.log(response.data);
        const data = response.data;
        const pagination = { ...this.state.pagination };

        pagination.current = data.current_page
        pagination.total = data.total;
        pagination.pageSize = parseInt(data.per_page);

        if (response.status === 200) { //OK
          this.setState({
            loading: false,
            data: data.data,
            pagination,
          })
        }
        const idUser = data.data.map(item => item.id)
        this.setState({ selectedUserKeys: idUser })
        // console.log("id user", idUser)
      })
      .catch(error => {
        this.setState({ loading: false });
        handleApiError(error, this.props)
      })

  }

  assignUser = (data) => {
    //const { id, qty_allocated } = data
    // console.log("qty_allocated", qty_allocated)
    this.setState({
      visible: true,
      // id_header: id,
      // data_header: qty_allocated,
    });
  }

  //cancel handle in modal
  handleCancel = (e) => {
    // console.log(e);
    this.setState({
      visible: false,
    });
  }

  visibleModal = (data) => {
    // console.log("visible modal false")
    this.setState({ visible: data })
    this.fetch();
  }

  //more action button
  handleMenuClick(e, id, data) {
    // console.log('click menu button', e, data);
    switch (e.key) {
      case "change-log":
        this.showModal(data)
        break;
      default:
        break;
    }
  }

  showModal = (data) => {
    // console.log(data)
    const { created_at, created_by, updated_at, updated_by } = data
    this.setState({
      created_at, created_by, updated_at, updated_by
    })

    this.setState({
      visibleCL: true,
    });
  }

  //cancel handle in modal
  handleCancelCL = (e) => {
    // console.log(e);
    this.setState({
      visibleCL: false,
    });
  }

  confirmMassDeleteData() {
    const { selectedRowKeys } = this.state
    Modal.confirm({
      title: `Are you sure delete ${selectedRowKeys.length} data?`,
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      onOk: () => {
        this.massDeleteData()
      },
      onCancel: () => {
        // console.log('Cancel');
      },
    });
  }

  massDeleteData() {
    // console.log("selected key",this.state.selectedRowKeys)
    // console.log("props",this.props)
    const { roleId } = this.props
    const id = roleId
    api.deleteUserAssign(id, { user_id: this.state.selectedRowKeys })
      .then(response => {
        // console.log(response.data)
        message.success('Delete data success');
        this.fetch();
        this.setState({ selectedRowKeys: [] })
      })
      .catch(error => {
        // console.log(error.response)
        message.error('Failed to delete');
        handleApiError(error, this.props)
      });
  }

  //on check table item
  onSelectChange = (selectedRowKeys) => {
    // console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  }

  render() {
    //this is for modal
    const { created_at, updated_at, created_by, updated_by } = this.state
    const labelCol = {
      xs: { span: 24 },
      sm: { span: 16 },
      md: { span: 8 },
    }
    const wrapperCol = {
      xs: { span: 24 },
      sm: { span: 16 },
      md: { span: 8 },
    }

    let { selectedRowKeys } = this.state
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;

    //disable input in view mode
    const inputDisabled = this.props.view
    console.log(inputDisabled)

    const columns = [
      {
        title: 'Name',
        dataIndex: 'fullname',
        sorter: true,
        width: '30%'
      },
      {
        title: 'Email',
        dataIndex: 'email',
        sorter: true,
        width: '25%'
      },
      {
        title: 'Assigned Date',
        render: (record) => {
          const role = record.roles[0]
          if (role) {
            const date = record.roles[0].pivot.created_at
            return dateFormat(date)
          }
        },
      },
      // {
      //   title: '',
      //   dataIndex: 'id',
      //   render:(id, data)=> (
      //     <div>
      //       <Dropdown overlay={(
      //         <Menu onClick={e=>this.handleMenuClick(e,id, data)}>
      //           <Menu.Item key="change-log"><Icon type="file-done" />Change Log</Menu.Item>
      //         </Menu>
      //       )}>
      //         <Button size="small"><Icon type="ellipsis"/></Button>              
      //       </Dropdown>
      //     </div>
      //   ),
      //   width:50
      // }    
    ]

    return (
      <div style={styles.content}>
        <Row>
          <Col span={8}>
            {/* <Tooltip title="Create">
              <Button onClick={this.assignUser}
                type="primary"
                style={{ marginBottom: 16 }}><PlusOutlined /></Button>
            </Tooltip>
            <Tooltip title="Delete">
              <Button onClick={this.confirmMassDeleteData}
                disabled={!hasSelected}
                type="danger"
                style={{ marginLeft: 10 }}><DeleteOutlined /></Button>
            </Tooltip> */}

            {/* {inputDisabled ? null : 
              <AuthObject allow={authobjectCfg.create}>
                <Tooltip title="Create">
                  <Button onClick={this.assignUser}
                    type="primary" 
                    style={{ marginBottom: 16 }}><Icon type="plus" /></Button>
                </Tooltip>
              </AuthObject>
            }
            {inputDisabled ? null : 
              <AuthObject allow={authobjectCfg.update}>
                <Tooltip title="Delete">
                  <Button onClick={this.confirmMassDeleteData}
                    disabled={!hasSelected}
                    type="danger" 
                    style={{ marginLeft: 10 }}><Icon type="delete" /></Button>
                </Tooltip>
              </AuthObject>
            } */}
          </Col>
          <Col span={4} push={12}>
            <Input.Search
              placeholder="Search"
              onSearch={this.searchData}
              style={{ marginBottom: 16 }}
            />
          </Col>
        </Row>

        <Table
          columns={columns}
          rowKey={record => record.id}
          dataSource={this.state.data}
          pagination={{
            pageSizeOptions: ['20', '50', '100', '200'],
            showSizeChanger: true,
            showQuickJumper: true,
            showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
            ...this.state.pagination
          }}
          loading={this.state.loading}
          scroll={{ y: 400 }}
          onChange={this.handleTableChange}
          rowSelection={hasAuthObject(authobjectCfg.update) && !inputDisabled ? rowSelection : null}
          size="middle"
        />

        <Modal
          title="Assign User"
          open={this.state.visible}
          onCancel={this.handleCancel}
          footer={null}
        >
          <PopupAssignUser
            role={this.state.role}
            data={this.state.selectedUserKeys}
            closeModal={this.visibleModal}
          />
        </Modal>

        <Modal
          title="Change Log"
          open={this.state.visibleCL}
          onCancel={this.handleCancelCL}
          footer={null}
        >
          <Row style={styles.row}><Col {...labelCol}>Created by</Col><Col {...wrapperCol}>{created_by ? created_by.name : 'system'}</Col></Row>
          <Row style={styles.row}><Col {...labelCol}>Created date</Col><Col {...wrapperCol}>{dateFormat(created_at)}</Col></Row>
          <Row style={styles.row}><Col {...labelCol}>Updated by</Col><Col {...wrapperCol}>{updated_by ? updated_by.name : '-'}</Col></Row>
          <Row style={styles.row}><Col {...labelCol}>Updated date</Col><Col {...wrapperCol}>{dateFormat(updated_at)}</Col></Row>
        </Modal>
      </div>
    );
  }
}

const styles = {
  wrapper: {
  },
  heading: {
    padding: '10px 25px',
    backgroundColor: '#fff'
  },
  content: {
    padding: 15,
    backgroundColor: '#fff'
  },
  panel: {
    border: 0
  }
}


export default Page;