import React from 'react';
import {
    Form, Input, Divider, Tooltip, Button, Card, Message, Space
} from 'antd';
import {
    SaveOutlined,
    CloseCircleOutlined,
    ArrowLeftOutlined,
    ReloadOutlined
} from '@ant-design/icons';
import hasAuthObject from '../../../helpers/hasAuthObject';
import Api from '../../../helpers/api';
import handleApiError from '../../../helpers/handleApiError';

const authobjectCfg = {
    view: 'role-view', //button display detail
    create: 'role-create', //button add
    update: 'role-update', //button edit, delete
}

const FormItem = Form.Item;
const api = new Api;

class FormRole extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            errors: {},
            loading: false,
            formRole: {
                name: ''
            }
        }
    }

    componentDidMount = () => {
        const { edit, view } = this.props

        if (edit || view) {
            this.getEditData()
        }
    }

    getEditData() {
        const { id } = this.props.match.params
        // console.log(this.props.match.params)

        api.viewRole(id)
            .then(response => {
                // console.log(response.data)

                let formRoleNew = { ...this.state.formRole }
                formRoleNew['name'] = response.data.name
                this.setState({
                    formRole: formRoleNew
                }, () => {
                    console.log(this.state.formRole)
                })
            })
            .catch(error => {
                console.log(error)
                handleApiError(error, this.props)
            })
    }

    handleChange = (event) => {
        let formRoleNew = { ...this.state.formRole }
        formRoleNew[event.target.name] = event.target.value
        this.setState({
            formRole: formRoleNew
        }, () => {
            console.log(this.state.formRole)
        })
    }

    handleSubmit = () => {
        const { formRole } = this.state
        this.setState({ loading: true });

        console.log('Received values of form: ', formRole);

        let apiPost
        //handling if form add
        if (this.props.add) {
            const { role } = this.props
            //on form add, after save role, form become update
            if (!role) {
                apiPost = api.addRole(formRole)
            } else {
                apiPost = api.updateRole(this.state.id_hash, formRole)
            }
        } else if (this.props.edit) {
            //handling if form edit
            const { id } = this.props.match.params
            apiPost = api.updateRole(id, formRole)
        }

        apiPost.then(response => {
            this.setState({ loading: false })
            const data = response.data
            console.log(response);
            Message.success('save data success');
            // this.props.history.goBack();

            this.setState({ loading: false, errors: {}, id_hash: data.id_hash })
            this.props.onSuccess(data);

        }).catch(error => {
            this.setState({ loading: false })
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                this.setState({ errors: errors })
                // console.log(this.state.errors.name);
            }
            handleApiError(error, this.props)
        })

    }

    handleReset = () => {
        this.setState({
            formRole: {
                name: ''
            }
        })
    }

    render() {
        // console.log(this.props)
        const { view, edit } = this.props
        const { errors } = this.state;

        //disable input in view mode
        const inputDisabled = view
        console.log(inputDisabled)

        return (
            <Card>
                <Form name="complex-form" onFinish={this.handleSubmit} labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}>
                    <FormItem
                        label="Name"
                        validateStatus={errors && errors.name ? 'error' : undefined}
                        help={errors ? errors.name : null}
                        rules={[{ required: false, message: 'Please input name' }]}
                    >
                        <Input
                            disabled={inputDisabled}
                            name="name"
                            value={this.state.formRole.name}
                            onChange={this.handleChange} />
                    </FormItem>

                    {!inputDisabled ?
                        <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required field</p>
                        : null}
                    <Divider />

                    <Space>
                        <Button onClick={() => this.props.history.goBack()}
                            icon={<ArrowLeftOutlined />}
                        >Back</Button>

                        {!inputDisabled ?
                            <Button onClick={this.handleReset}
                                icon={<ReloadOutlined />}
                            >Reset</Button>
                            : null}

                        {!inputDisabled ?
                            <Button type="primary" htmlType="submit" loading={this.state.loading}
                                icon={<SaveOutlined />}
                            >Save</Button>
                            : null}
                    </Space>
                </Form>
            </Card>
        )
    }
}

export default FormRole;