import React, { Component } from 'react'
import { Form } from 'antd';
import Api from '../../../helpers/api';

import TableUserList from './TableUserList'
import handleApiError from '../../../helpers/handleApiError';

const api = new Api();

export class FormUserList extends Component {
  constructor() {
    super()
    this.state = {
      errors: {},
      users: [], //options users
      selectedUserKeys: [],
    }

    this.table = React.createRef();
    // this.fetchUserList = debounce(this.fetchUserList, 300)
  }

  async componentDidMount() {
    const { edit, view, } = this.props
    if (edit || view) {
      await this.getEditData()
    }

    // this.fetchUserList()
  }

  getEditData() {
    // console.log("geteditdata")
    const { role, add, match } = this.props
    //console.log("role", role)
    let id

    if (add) {
      id = role.id
    } else {
      id = match.params.id
    }

    this.setState({ loading: true });

    api.viewRole(id)
      .then(response => {
        this.setState({ loading: false });
        // console.log(response.data)
        const { users, } = response.data

        let user = users.map((item) => item.id)

        this.setState(
          { selectedUserKeys: user },
        )

        //set form value with this
        this.props.form.setFieldsValue({
          user_id: user
        });
      })
      .catch(error => {
        // console.log(error)
        this.setState({ loading: false });
        handleApiError(error, this.props)
      })
  }

  render() {
    // console.log("render")
    const { role, add, match, view } = this.props

    // get current add/edited role id
    let roleId
    if (add) {
      roleId = role.id_hash
    } else {
      roleId = match.params.id
    }

    return (
      <div>

        {this.state && this.state.selectedUserKeys &&
          <TableUserList
            ref={this.table}
            roleId={roleId}
            role={role}
            data={this.state.selectedUserKeys}
            view={view}
          />
        }

        {/* <Form className="mnd-form"
            onSubmit={this.handleSubmit}>

            <Row>
              <Col span={18}>
                <SelectModal.User
                  {...formItemLayout}
                  form={form}
                  mode="multiple"
                  label="User List"
                  fieldName="user_id"
                  selectedKeys={this.state.user_id}
                  onChange={this.handleSelectUser}
                  disabled={inputDisabled}
                  validateStatus={errors && errors.user_id ? 'error' : undefined}
                  help={errors ? errors.user_id : null }
                />
              </Col>
            </Row>


            <Divider />

            <FormItem>
              {!inputDisabled ?
                <Tooltip title="Save">
                  <Button type="primary" loading={loading} disabled={loading} icon="save" htmlType="submit" style={{ marginLeft: 10 }} />
                </Tooltip>
                : null}
                        
            </FormItem>
          </Form> */}

      </div>
    )
  }
}

export default FormUserList
