import React from 'react';
import { Layout, Tabs, Row, Col, Divider } from 'antd';
import FormRole from './FormRole'
import FormOrganizationParameter from './FormOrganizationParameter'

import FormAuthObject from './FormAuthObject'
import FormUserList from './FormUserList'

const { Content } = Layout;
const TabPane = Tabs.TabPane;



/**
 * this form is for add and edit page
 * please consider using `add` or `edit` props
 * 
 */

class FormIndex extends React.Component {
    constructor() {
        super();

        this.state = {
            role: null,

            pageTitle: '',
            activeTab: '1',
            confirmDirty: false,
            autoCompleteResult: [],
            parameters: [],
        }

        // this.onCreateRoleSuccess = this.onCreateRoleSuccess.bind(this)
        // this.onViewRoleSuccess = this.onViewRoleSuccess.bind(this)
        // this.onCreateParameterSuccess = this.onCreateParameterSuccess.bind(this)
        // this.onCreateAuthObjectSuccess = this.onCreateAuthObjectSuccess.bind(this)
        // this.onCreateUserListSuccess = this.onCreateUserListSuccess.bind(this)
        this.onTabClick = this.onTabClick.bind(this)

    }

    async componentDidMount() {
        // console.log(this.props)
        const { add, edit, view } = this.props
        //set page form type
        let pageTitle
        switch (true) {
            case add:
                pageTitle = 'Create'
                break
            case edit:
                pageTitle = 'Edit'
                break
            case view:
                pageTitle = 'Display'
                break
        }

        const { id } = this.props.match.params
        // console.log('id ' + id)
        //set role id for child form
        this.setState({ role: id ? { id } : null })
    }

    onCreateRoleSuccess(data) {
        // console.log('onCreateRoleSuccess', data)
        this.setState({ role: data }, () => { console.log(this.state.role) })
    }

    onViewRoleSuccess(data) {
        // console.log('onViewRoleSuccess', data)
        this.setState({ role: data })
    }

    onTabClick(key) {
        // console.log(key)
        if (this.props.add) {
            //can click to other tab if role added first
            if (this.state.role) {
                this.setState({ activeTab: key })
            }
        } else {
            this.setState({ activeTab: key })
        }
    }

    onCreateParameterSuccess(data) {
        // console.log(data)
    }

    onCreateAuthObjectSuccess(data) {
        // console.log(data)
    }

    onCreateUserListSuccess(data) {
        // console.log(data)
    }

    render() {
        const { role } = this.state;
        console.log(this.state.role)

        return (
            <Content style={styles.wrapper}>

                {/* this is form role */}
                <FormRole {...this.props} //add/edit props
                    role={role}
                    onSuccess={this.onCreateRoleSuccess}
                    onViewSuccess={this.onViewRoleSuccess} />
                {role ?
                    <Tabs defaultActiveKey="1"
                        activeKey={this.state.activeTab}
                        onTabClick={this.onTabClick}
                        style={styles.content}>

                        <TabPane tab="Filter Data by" disabled={role.composite} key="1">
                            <Row gutter={15}>
                                <Col span={12}>
                                    <div>
                                        <h2>Filter Data by</h2>
                                        <FormOrganizationParameter edit {...this.props} history={this.props.history}
                                            role={role}
                                            onSuccess={this.onCreateParameterSuccess} />
                                    </div>
                                </Col>
                                <Col span={12}>

                                </Col>
                            </Row>
                        </TabPane>

                        <TabPane tab="Menu Object" disabled={role.composite} key="2">
                            <Row gutter={15}>
                                <Col>
                                    {/* this is form select auth object */}
                                    <div>
                                        <h2>Menu Object</h2>
                                        <FormAuthObject {...this.props}
                                            role={role}
                                            onSuccess={this.onCreateAuthObjectSuccess} />
                                    </div>
                                </Col>
                                <Col>
                                </Col>
                            </Row>
                        </TabPane>

                        <TabPane tab="User List" disabled={role.composite} key="3">
                            <Row gutter={15}>
                                <Col>
                                    <div>
                                        <h2>Select Users</h2>
                                        <FormUserList {...this.props}
                                            role={role}
                                            onSuccess={this.onCreateUserListSuccess} />
                                    </div>
                                </Col>
                            </Row>
                        </TabPane>

                    </Tabs>
                    : null}
            </Content>
        );
    }
}

const styles = {
    wrapper: {
    },
    heading: {
        padding: '15px 25px',
        backgroundColor: '#fff',
    },
    content: {
        marginTop: 15,
        marginRight: 2,
        marginLeft: 2,
        padding: 25,
        backgroundColor: '#fff'
    },
}

export default FormIndex;