import React, { Component } from 'react'
import { Form, Button, message, Divider, Transfer } from 'antd';
import debounce from 'lodash/debounce'
import Api from '../../../helpers/api';
import {
    SaveOutlined,
} from '@ant-design/icons';
import handleApiError from '../../../helpers/handleApiError';

const api = new Api();

export class FormAuthObject extends Component {
    constructor() {
        super()
        this.state = {
            errors: {},
            // optionsModule: [], //options module
            availableAuthObjects: [], //available auth objects to assign
            targetKeys: [], //assigned auth object to role
        }

        this.fetchModules = debounce(this.fetchModules, 300)
        this.getAvailableData = debounce(this.getAvailableData, 300)
    }

    async componentDidMount() {

        // await this.fetchModules()
        await this.getAvailableData()
        const { edit, view } = this.props

        if (edit || view) {
            this.getEditData()
        }
    }

    getEditData() {

        //const { id } = this.props.match.params
        const { role, add, match } = this.props
        let id

        if (add) {
            id = role.id_hash
        } else {
            id = match.params.id
        }

        this.setState({ loading: true });

        api.viewRole(id)
            .then(response => {
                this.setState({ loading: false });
                // console.log(response.data)
                const { modules } = response.data

                let module = modules.map((item) => item.id)

                this.setState({
                    // optionsModule: [...this.state.optionsModule, ...modules], 
                    availableAuthObjects: [...this.state.availableAuthObjects],
                    targetKeys: [...module]
                })

                //set form value with this
                // this.props.form.setFieldsValue({
                //     module
                // });
            })
            .catch(error => {
                // console.log(error)
                this.setState({ loading: false });
                handleApiError(error, this.props)
            })
    }

    handleSubmit = () => {
        const { targetKeys } = this.state

        this.setState({ loading: true });

        const dataToPost = { module: targetKeys }

        let apiPost

        const { role, add, match } = this.props
        let id
        if (add) {
            id = role.id_hash
        } else {
            id = match.params.id
        }
        apiPost = api.updateAuthObject(id, dataToPost)


        apiPost.then(response => {
            message.success('save data success');
            this.setState({ loading: false, errors: {} })
        }).catch(error => {
            if (error) {
                const { errors } = error.response.data
                this.setState({ loading: false, errors });
            }
            handleApiError(error, this.props)
        })

    }

    handleReset = () => {
        this.props.form.resetFields();
    }

    //populate select module/auth object
    fetchModules(value) {
        api.getMasterModules({ q: value })
            .then(response => {
                if (response.status === 200) { //OK
                    const { data } = response.data
                    this.setState({
                        optionsModule: data,
                    })
                }
            })
    }

    handleSearch = (value) => {
        this.fetchModules(value)
    }

    //get available auth object for Transfer
    getAvailableData(value) {
        api.getAllModules({ q: value })
            .then(response => {
                if (response.status === 200) { //OK
                    const data = response.data

                    this.setState({
                        availableAuthObjects: [...this.state.availableAuthObjects, ...data]
                    })
                }
            })
    }

    //handle Transfer Change
    handleTransferChange = (targetKeys) => {
        this.setState({ targetKeys });
    }

    //handle click search on Transfer
    handleTransferSearch = (dir, value) => {
        // console.log('search:', dir, value);
    };

    handleTransferFilterOption = (inputValue, option) => {
        return option.object.indexOf(inputValue) > -1
    }

    render() {
        const { view } = this.props
        const { errors, loading } = this.state;


        //disable input in view mode
        const inputDisabled = view

        return (
            <Form className="mnd-form"
                onFinish={this.handleSubmit}>

                {errors && errors.module ? <p className="text-error">Please assign Auth Object.</p> : null}

                <Transfer
                    showSearch
                    listStyle={{
                        width: 400,
                        height: 500,
                    }}
                    titles={['Available Menu Object', 'Assigned Menu Object']}
                    disabled={inputDisabled}
                    rowKey={record => record.id}
                    dataSource={this.state.availableAuthObjects}
                    targetKeys={this.state.targetKeys}
                    filterOption={this.handleTransferFilterOption}
                    onChange={this.handleTransferChange}
                    onSearch={this.handleTransferSearch}
                    onScroll={this.handleTransferScroll}
                    render={record => record.object}
                />

                <Divider />

                {!inputDisabled ?
                    <Button type="primary" htmlType="submit" loading={loading}
                        disabled={loading}
                        icon={<SaveOutlined />}
                    >Save</Button>
                    : null}

            </Form>
        )
    }
}

export default FormAuthObject
