import React, { Component } from 'react'
import { Form, Select, Button, Message, Divider } from 'antd';
// import debounce from 'lodash/debounce';
import {
  SaveOutlined,
} from '@ant-design/icons';
import Api from '../../../helpers/api';
import handleApiError from '../../../helpers/handleApiError';

const FormItem = Form.Item;
const Option = Select.Option;
const api = new Api();

export class FormOrganizationParameter extends Component {
  constructor() {
    super()
    this.state = {
      errors: {},
      plant: [],
      room: [],
      vendor: [],
      formOrgParam: {
        plant: [],
        room: [],
        vendor: [],
      },
      loading: false
    }

    // this.fetchPlant = debounce(this.fetchPlant, 300)
    // this.fetchRoom = debounce(this.fetchRoom, 300)
  }

  componentDidMount() {
    this.fetchPlant()
    this.fetchRoom()
    this.fetchVendor()
    const { edit, view } = this.props

    if (edit || view) {
      this.getEditData()
    }
  }

  getEditData() {

    const { role, add, match } = this.props
    let id

    if (add) {
      id = role.id_hash
    } else {
      id = match.params.id
    }
    this.setState({ loading: true });

    api.viewRole(id)
      .then(response => {
        this.setState({ loading: false });
        const { organization_parameter_value } = response.data

        console.log('org val', organization_parameter_value)

        let { plant, room, vendor } = organization_parameter_value
        plant = plant === null ? [] : plant
        room = room === null ? [] : room
        vendor = vendor === null ? [] : vendor

        // set form value with this
        this.setState({
          formOrgParam: {
            plant,
            room,
            vendor,
          }
        }, () => {
          console.log(this.state.formOrgParam)
        })
      })
      .catch(error => {
        console.log(error)
        this.setState({ loading: false });
        handleApiError(error, this.props)
      })
  }

  handleSubmit = (e) => {
    this.setState({ loading: true });
    const { formOrgParam } = this.state
    let apiPost
    //handling if form add
    if (this.props.add) {
      const { role } = this.props
      const { id_hash } = role
      apiPost = api.addOrganizationParameter(id_hash, formOrgParam)
    } else if (this.props.edit) {
      //handling if form edit
      const id = this.props.match.params.id
      apiPost = api.updateOrganizationParameter(id, formOrgParam)
    }

    apiPost.then(response => {
      Message.success('save data success');
      this.setState({ loading: false })
    }).catch(error => {
      if (error) {
        const { errors } = error.response
        this.setState({ loading: false, errors });
      }
      handleApiError(error, this.props)
    })
  }

  fetchPlant(value) {
    api.getPlant({ q: value })
      .then(response => {
        if (response.status === 200) { //OK
          const { data } = response.data

          this.setState({
            plant: data,
          }, () => this.state.plant)
        }
      })
  }

  fetchRoom(value) {
    api.getRoom({ q: value })
      .then(response => {
        if (response.status === 200) { //OK
          const { data } = response.data

          this.setState({
            room: data,
          }, () => this.state.room)
        }
      })
  }

  fetchVendor(value) {
    api.getVendor({ q: value })
      .then(response => {
        if (response.status === 200) { //OK
          const { data } = response.data

          this.setState({
            vendor: data,
          }, () => this.state.vendor)
        }
      })
  }

  handleSearchPlant = (value) => {
    this.fetchPlant(value)
  }

  handleSearchRoom = (value) => {
    this.fetchRoom(value)
  }

  handleSearchVendor = (value) => {
    this.fetchVendor(value)
  }

  handleSelectedPlant = (key, value) => {
    let formOrgParamNew = { ...this.state.formOrgParam }

    formOrgParamNew['plant'] = value.map(item => {
      return item.value
    })

    this.setState({
      formOrgParam: formOrgParamNew
    }, () => this.fetchPlant(''))
  }

  handleSelectedRoom = (key, value) => {
    let formOrgParamNew = { ...this.state.formOrgParam }

    formOrgParamNew['room'] = value.map(item => {
      return item.value
    })

    this.setState({
      formOrgParam: formOrgParamNew
    }, () => this.fetchRoom(''))
  }

  handleSelectedVendor = (key, value) => {
    let formOrgParamNew = { ...this.state.formOrgParam }

    formOrgParamNew['vendor'] = value.map(item => {
      return item.value
    })

    this.setState({
      formOrgParam: formOrgParamNew
    }, () => this.fetchVendor(''))
  }

  render() {
    const { view } = this.props
    const { loading, plant } = this.state;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
        md: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
        md: { span: 16 },
      },
    };

    //disable input in view mode
    const inputDisabled = view

    return (
      <Form className="mnd-form"
        onFinish={this.handleSubmit}>

        <FormItem
          {...formItemLayout}
          label="Plant"
        >
          <Select
            mode="multiple"
            allowClear
            filterOption={false}
            style={{ width: '100%' }}
            value={this.state.formOrgParam.plant}
            onChange={this.handleSelectedPlant}
            onSearch={this.handleSearchPlant}
          >
            {this.state.plant.map((r) => (
              <Option key={r.id} value={r.id}>{r.name}</Option>
            ))}
          </Select>
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="Room"
        >
          <Select
            mode="multiple"
            allowClear
            filterOption={false}
            style={{ width: '100%' }}
            value={this.state.formOrgParam.room}
            onChange={this.handleSelectedRoom}
            onSearch={this.handleSearchRoom}
          >
            {this.state.room.map((r) => (
              <Option key={r.id} value={r.id}>{r.name}</Option>
            ))}
          </Select>
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="Vendor"
        >
          <Select
            mode="multiple"
            allowClear
            filterOption={false}
            style={{ width: '100%' }}
            value={this.state.formOrgParam.vendor}
            onChange={this.handleSelectedVendor}
            onSearch={this.handleSearchVendor}
          >
            {this.state.vendor.map((r) => (
              <Option key={r.id} value={r.id}>{r.name}</Option>
            ))}
          </Select>
        </FormItem>

        <Divider />

        {!inputDisabled ?
          <Button type="primary" htmlType="submit" loading={loading}
            disabled={loading}
            icon={<SaveOutlined />}
          >Save</Button>
          : null}
      </Form>
    )
  }
}

export default FormOrganizationParameter
